.showcase-container {
    position: relative;
    height: 650px;
    background: linear-gradient( rgba(0, 0, 5, 0.55), rgba(0, 0, 5, 0.55) ), url("./../../../../img/home-1.jpeg");
    background-position-y: 0px;
    background-size: cover;
    background-position: 20%;
    -webkit-animation-name: slide-in;
    -webkit-animation-duration: 1.5s;
    // -webkit-animation-name: fade-in;
    // -webkit-animation-duration: 2s;
    // animation: slideIn 0.6s cubic-bezier(0.54, 0.46, 0.54, 0.94) 2s forwards;
    // animation: fadeIn 0s ease-in 0s forwards;}
}

    @keyframes fade-in {
      from {opacity: 0;}
      to {opacity: 1;}
    }

    @media only screen and (min-width: 850px) {
    @keyframes slide-in {
      from {height:0;
        opacity: 0;
      }
      to{height:650px;
        opacity: 1;}
    }
  }
  @media only screen and (max-width: 850px) {
    @keyframes slide-in {
      from {height:0;
        opacity: 0;
      }
      to{height:300px;
        opacity: 1;}
    }
  }

    

  .showcase {
    width:100%;
    height:100%;}

  .team-logo-container{
    height: 150px;}

    .team-logo {
      position: relative;
      bottom:30px;
      height:150%;
      padding:auto auto 80px auto;}

  .draw-line {
    height:2px;
    width:800px;
    background-color:#ccc;
    margin:auto;}

  .team-name {
    padding: 23px 0;
    font-size: 80px;
    color: #fff;}

  .culture-container {
    display: flex;
    margin-top: 170px;
    justify-content: center}

  .culture-text {
    color: rgb(218, 218, 218);
    font-size: 35px;}



@media only screen and (max-width: 850px) {
  .showcase-container { 
    height: 300px;}

    .team-logo-container{
      height: 90px;}

      .team-logo {
        position: relative;
        bottom:10px;
        height:120%;
        padding:auto auto 80px auto;}

    .draw-line {
      height:1px;
      width:60%;
      background-color:#ccc;
      margin:auto;}

    .team-name {
      padding: 6px 0;
      font-size: 35px;
      color: #fff;}
    
    .culture-container {
      display: flex;
      margin-top: 60px;
      justify-content: center}

      .culture-text {
        padding-right:15px;
        color: rgb(218, 218, 218);
        font-size: 15px;}
}

